@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap');


:root{
    --primary: #07d100;
    --secondary: #6c6e6e;
    --error: #ff4a4a;
}
*{
  background-color: rgb(214, 220, 224);
}


/* base styles & title */
body{
    font-family: "Noto Serif";
    color: var(--secondary);
  }
  .App{
    max-width: 960px;
    margin: 0 auto;
  }
  .title h1{
    color: var(--primary);
    font-size: 1.2rem;
    letter-spacing: 2px;
    font-weight: normal;
  }
  .title h2, .title p{
    text-align: center;
  }
  .title h2{
    margin-top: 60px;
    font-size: 2.6rem;
  }
  
  /* upload form styles */
  form{
    margin: 30px auto 10px;
    text-align: center;
  }
  label input{
    height: 0;
    width: 0;
    opacity: 0;
  }
  label{
    display: block;
    width: 20%;
    height: 30px;
    border: 1px solid var(--primary);
    border-radius: 20px;
    margin: 10px auto;
    line-height: 30px;
    color: var(--primary);
  }
  label:hover{
    color: rgb(0, 24, 240);
    border: 1px solid rgb(0, 24, 240);
    font-weight: bold;

  }
  .output{
    height: 60px;
    font-size: 0.8rem;
  }
  .error{
    color: var(--error);
  }
  
  /* progress bar styles */
  .progress-bar{
    height: 5px;
    background: var(--primary);
    margin-top: 20px;
  }
  
  /* image grid styles */
  .img-grid{
    margin: 20px auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 40px;
  }
  .img-wrap{
    overflow: hidden;
    height: 0;
    padding: 50% 0;
    /* padding controls height, will always be perfectly square regardless of width */
    position: relative;
    border-radius: 10px;
    box-shadow: 6px 5px rgba(0,0,0,0.2);
    opacity: 0.8;
  }
  
  .img-wrap img{
    min-width: 100%;
    min-height: 100%;
    max-width: 150%;
    position: absolute;
    top: 0;
    left: 0;
    box-shadow: 1px 1px 2px black;
    
  }
  
  /* modal styles */
  .backdrop{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
  }
  .backdrop img{
    display: block;
    max-width: 60%;
    max-height: 80%;
    margin: 60px auto;
    box-shadow: 3px 5px 7px rgba(0,0,0,0.5);
    border: 3px solid white;
  }